import { Spinner } from "../ui/spinner"
import { useEffect, useState } from "react"
import { SquarePen, FileText, HelpCircle, Code } from "lucide-react"
import { LinkCard } from "../ui/link-card"

interface Conversation {
    id: string
    lastMessage: string
    timestamp: Date
    status: 'active' | 'resolved'
}

const EmptyInbox = () => {
    return (
        <div className="flex-1">
            <div className="mt-4">
                <p className="font-medium text-gray-900">You have no conversations</p>
            </div>

            <div className="mt-8">
                <h3 className="text-sm font-medium text-gray-900 mb-3">Helpful resources</h3>
                <div className="space-y-2">
                    <LinkCard
                        href="#"
                        icon={<FileText className="w-5 h-5" />}
                        title="Getting started guide"
                        subtitle="Learn the basics of using our platform"
                    />
                    <LinkCard
                        href="#"
                        icon={<HelpCircle className="w-5 h-5" />}
                        title="Frequently asked questions"
                        subtitle="Find answers to common questions"
                    />
                    <LinkCard
                        href="#"
                        icon={<Code className="w-5 h-5" />}
                        title="API documentation"
                        subtitle="Technical details for developers"
                    />
                </div>
            </div>
        </div>
    )
}

const Inbox = () => {
    const [isLoading, setIsLoading] = useState(true)
    const [conversations, setConversations] = useState<Conversation[]>([])

    useEffect(() => {
        // Simulate loading delay
        const timer = setTimeout(() => {
            setIsLoading(false)
        }, 3000)

        return () => clearTimeout(timer)
    }, [])

    if (isLoading) {
        return (
            <div className="flex-1 flex flex-col items-center justify-center">
                <Spinner />
                <p className="text-sm text-muted-foreground mt-2">Loading messages...</p>
            </div>
        )
    }

    return (
        <div className="flex-1 flex relative">
            <div className="flex-1 animate-fadeIn">
                <p className="text-sm text-gray-700 mb-6">ACME virtual assistant is here for you 24/7. Our human team will get back to you within 48 hours.</p>
                <h2 className="text-lg font-bold">Messages</h2>
                <EmptyInbox />
            </div>
            
            <button className="absolute bottom-4 right-4 p-3 bg-primary text-primary-foreground rounded-lg shadow-lg hover:scale-105 transition-transform">
                <SquarePen className="w-5 h-5" />
            </button>
        </div>
    )
}

export default Inbox