import { Button } from "@/components/ui/button"
import { X } from "lucide-react"
import { WelcomeScreen } from "./WelcomeScreen"
import Inbox from "./Inbox"
import { useState } from "react"

type ChatProps = {
    toggleChat: () => void
}

export const ChatWindow = ({ toggleChat }: ChatProps) => {
    const [showInbox, setShowInbox] = useState(false)
    
    const handleStartConversation = () => {
        setShowInbox(true)
    }
    
    return (
        <div className="bg-white rounded-lg shadow-lg w-90 h-150 flex flex-col overflow-hidden animate-in slide-in-from-bottom-5 duration-300">
            <div className="bg-primary p-2 flex justify-between items-center">
                <div className="w-8"></div> {/* Spacer to balance the close button */}
                <h3 className="text-primary-foreground font-medium text-center flex-1">ACME Messaging</h3>
                <Button
                    variant="ghost"
                    size="icon"
                    className="h-8 w-8 text-primary-foreground hover:text-primary-foreground hover:bg-primary-secondary cursor-pointer"
                    onClick={toggleChat}
                    aria-label="Minimize chat"
                >
                    <X className="h-4 w-4" />
                </Button>
            </div>
            <div className="flex flex-1 p-4">
                {showInbox ? (
                    <div className="flex flex-1 animate-in fade-in slide-in-from-right-5 duration-300">
                        <Inbox />
                    </div>
                ) : (
                    <WelcomeScreen onStartConversation={handleStartConversation} />
                )}
            </div>
        </div>
    )
}